<template>
  <!---->
  <div class="box-bubble" v-show="bubbleShow">
    <div class="text-overflow-multi">
       <span>{{notice.user.nick_name}}</span>{{notice.time}} <span>{{notice.chest_prizes[0].name}}</span></div>
  </div>
</template>

<script>
  export default {
    props: ['boxId', 'boxType'],
    data () {
      return {
        notice: {user: {nick_name: ''}, time: '', chest_prizes: [{name: ''}]},
        bubbleShow: false,
        fixedData1: [
          {user: {nick_name: ''}, time: '1', chest_prizes: [{name: '5'}]},
          {user: {nick_name: ''}, time: '2', chest_prizes: [{name: 'Guhl'}]},
          {user: {nick_name: ''}, time: '3', chest_prizes: [{name: '18'}]},
          {user: {nick_name: ''}, time: '4', chest_prizes: [{name: '5'}]},
          {user: {nick_name: ''}, time: '5', chest_prizes: [{name: 'G&M VE'}]},
          {user: {nick_name: ''}, time: '6', chest_prizes: [{name: '5'}]},
          {user: {nick_name: ''}, time: '7', chest_prizes: [{name: ''}]},
          {user: {nick_name: ''}, time: '8', chest_prizes: [{name: '5'}]},
          {user: {nick_name: ''}, time: '9', chest_prizes: [{name: '30'}]},
          {user: {nick_name: ''}, time: '10', chest_prizes: [{name: '18'}]}
        ],
        fixedData2: [
          {user: {nick_name: ''}, time: '11', chest_prizes: [{name: '50'}]},
          {user: {nick_name: ''}, time: '12', chest_prizes: [{name: ' U'}]},
          {user: {nick_name: ''}, time: '13', chest_prizes: [{name: ''}]},
          {user: {nick_name: ''}, time: '14', chest_prizes: [{name: '1'}]},
          {user: {nick_name: ''}, time: '15', chest_prizes: [{name: '50'}]},
          {user: {nick_name: ''}, time: '16', chest_prizes: [{name: '20'}]},
          {user: {nick_name: ''}, time: '17', chest_prizes: [{name: ''}]},
          {user: {nick_name: ''}, time: '18', chest_prizes: [{name: '50'}]},
          {user: {nick_name: ''}, time: '19', chest_prizes: [{name: '100'}]},
          {user: {nick_name: ''}, time: '20', chest_prizes: [{name: '50'}]}
        ],
        fixedData3: [
          {user: {nick_name: ''}, time: '21', chest_prizes: [{name: ' 7 32G'}]},
          {user: {nick_name: ''}, time: '22', chest_prizes: [{name: '12'}]},
          {user: {nick_name: ''}, time: '23', chest_prizes: [{name: '100'}]},
          {user: {nick_name: ''}, time: '24', chest_prizes: [{name: '200'}]},
          {user: {nick_name: ''}, time: '25', chest_prizes: [{name: '60G'}]},
          {user: {nick_name: ''}, time: '26', chest_prizes: [{name: 'BeatsX'}]},
          {user: {nick_name: ''}, time: '27', chest_prizes: [{name: '100'}]},
          {user: {nick_name: ''}, time: '28', chest_prizes: [{name: 'MCM'}]},
          {user: {nick_name: ''}, time: '29', chest_prizes: [{name: '20'}]},
          {user: {nick_name: ''}, time: '30', chest_prizes: [{name: '100'}]}
        ]
      }
    },
    methods: {
      request: function () {
        this.$http({
          method: 'get',
          url: '/api/chest_boxs/current_notice?q[chest_box_id_eq]=' + this.boxId
        }).then(function (res) {
          let data = res.body;
          if (data.execErrCode == 200) {
            let allNotice = data.execData.chest_records, notice = [];
            for (let i = 0, len = allNotice.length; i < len; i++) {
              if (allNotice[i].chest_prizes.length > 0) {
                notice.push(allNotice[i]);
              }
            }
            if (notice.length > 0) {
              let n = Math.floor(Math.random() * notice.length);
              this.notice = notice[n];
              this.keyframe();
            }
          }
        }, function () {
          switch (this.boxType) {
            case '1':
              let n1 = Math.floor(Math.random() * this.fixedData1.length);
              this.notice = this.fixedData1[n1];
              break;
            case '2':
              let n2 = Math.floor(Math.random() * this.fixedData2.length);
              this.notice = this.fixedData2[n2];
              break;
            case '3':
              let n3 = Math.floor(Math.random() * this.fixedData3.length);
              this.notice = this.fixedData3[n3];
              break;
          }
          this.keyframe();
        })
      },
      keyframe: function () {
        let _this = this;
        _this.bubbleShow = true;
        setTimeout(function () {
          _this.bubbleShow = false;
        }, 2000)
      }
    },
    mounted(){
      let _this = this;
      window.timer11 = setInterval(function () {
        _this.request()
      }, 9000)
    },
    beforeDestroy: function () {
      clearInterval(window.timer11)
    }
  }
</script>

<style scoped>
  /*--*/
  .box-bubble {
    position: absolute;
    width: 8.32rem;
    height: 2.36rem;
    top: -1.11rem;
    left: -2.1rem;
    z-index: 90;
    background: url("../../images/box-bubble-bg.png") 0/100% 100%;
    font-size: .45rem;
    color: #000;
    line-height: 1.3;
    padding: .24rem .6rem .96rem;
    text-align: left;
  }

  .box-bubble span {
    color: #a44529;
  }
</style>



