<template>
<div>
  <div class="readRain" @click.stop="robRedBag" v-show="readRainShow">
    <canvas id="stars" v-show="canvasShow"></canvas>
    <div class="guide" v-if="timeShow">
      <div class="quan">
        <div class="quan1"></div>
        <div class="quan2"></div>
      </div>
      <div class="hand"></div>
      <p></p>
    </div>
    <!--  -->
    <div class="time" v-if="timeShow">
      {{this.SurplusTime}}
    </div>
    <div class="prediction" v-if="predictionShow" @click.stop="">
      <!--  -->
      <div class="prediction-box" :class="{'down': isHaveRedBag, 'up': isHaveRedBag,}" v-show="predictionboxshow">
        <div class="user-info">
          <div class="user-pic">
            <div class="bg"></div>
            <img :src="imgBaseUrl() + red_packages.user.head_url">
          </div>
          <div class="user-name">{{red_packages.user.nick_name || ""}}</div>
        </div>
        <div class="pro-pic">
          <div class="bg"></div>
          <img :src="imgBaseUrl() + red_packages.chest_prize.thumbnail">
        </div>
        <p class="pro-name text-overflow">{{red_packages.chest_prize.name || ""}}</p>
        <p class="txt"><br></p>
        <div class="fhb"></div>
      </div>
      <!-- 3 -->
      <div class="go" v-show="countShow">
        <img id="countImg" src="../../images/redbag-3.png">
        <div class="chuo">
        </div>
      </div>
      <div class="open-redbag" v-if="openBag">
        <div class="havebag" v-show="amount!=0">
          <div class="text">
            <p class="p1"></p>
            <p class="p2"><span class="money">{{amount}}</span></p>
          </div>
        </div>
        <div class="nobag" v-show="amount==0">
          <div class="text">
            <p><br></p>
          </div>
        </div>
        <button class="sure" @click.stop="sure"></button>
      </div>
    </div>
  </div>
  <div class="red-remind" @click.stop="remind" :class="{active: redremindShow}" v-show = "distanceTime>=0 && $route.path == '/openBox'">{{predictionM}}:{{predictionS}} </div>
</div>
</template>

<script>
  import {imgBaseUrl, mediaUrl} from '../../config/config'
  import $ from 'jquery'
  export default {
    data () {
      return {
        redbagVoice: '', //
        imgBaseUrl,
        mediaUrl,
        canvasShow: false,
        readRainShow: false,  //
        predictionShow: false,   //
        predictionboxshow: false, //
        isHaveRedBag: false,  //
        countShow: false,   //
        openBag: false, //
        timeShow: false,  //
        isStop: true,   // 
        amount: 0, //
        countImgUrl: 3,
        redremindShow: false, //
        predictionM: '00', //
        predictionS: '00', //
        redbagId: '', //
        token: '',           //accessToken
        countDownTimer: '', //
        red_packages: {     //
          chest_prize: {
            thumbnail: ''
          },
          user: {
            head_url: ''
          },
          end_time: ''},
        SurplusTime: 0, //
        distanceTime: -1,  //
      }
    },
    methods: {
      // 
      animateInit: function(){
        this.canvasShow = false
        this.readRainShow = false  //
        this.predictionShow = false   //
        this.predictionboxshow = false //
        this.isHaveRedBag = false  //
        this.countShow = false   //
        this.openBag = false //
        this.timeShow = false  //
        this.isStop = true // 
        this.countImgUrl = 3
        this.amount = 0, //
        this.redbagId = '' //
        this.predictionS = '00'
        this.SurplusTime = 0 //
        this.distanceTime = -1  //
      },
      //
      sure: function(){
        var _this = this
        this.animateInit()
        if(_this.$route.path == '/prizeList'){
          _this.$router.go(0)
        }
      },
      // 
      isPlay: function(){
        var _this = this
        if(_this.readRainShow == false){
          this.$http({
            method: 'get',
            url: '/api/red_packages',
            params: {id: this.boxId},
            headers: {accessToken: this.token}
          }).then(function(res){
            var data = res.body
            if(data.execErrCode==200){

                // 
                if (data.execData.red_packages.length !=0) {
                  if (data.execData.red_packages[0].id != localStorage.getItem("redbagId")){
                    _this.red_packages = data.execData.red_packages[0]
                    _this.SurplusTime = _this.red_packages.end_count_down - _this.red_packages.begin_count_down
                    _this.distanceTime = _this.red_packages.begin_count_down
                    _this.predictionCountDown()
                    window.localStorage.setItem("redbagId",data.execData.red_packages[0].id)
                    _this.redbagId = data.execData.red_packages[0].id

                  }

                  // 
                  // var correctionTimer = setInterval(function(){
                  //   if (_this.distanceTime<=0) {
                  //     clearInterval(correctionTimer)
                  //   };
                  //   _this.isPlay()
                  //   _this.distanceTime = _this.red_packages.begin_count_down
                  //   console.log(_this.distanceTime)
                  // }, 5000)

                };
            }else {

            }
          })
        }
      },
      // 
      start: function (){
        var _this = this
          this.isHaveRedBag = false;
          setTimeout(function(){
            _this.countShow = true
            _this.beginning();
          }, 1000)
      },
      //3,2,1,go
      beginning: function(){
        var _this = this
        _this.countImgUrl=3
        var countImg = document.getElementById("countImg")
        countImg.src = require('../../images/redbag-' + _this.countImgUrl +'.png')
        _this.addAudio(_this.mediaUrl + "/media/countdown.mp3")
        var countTimer = setInterval(function(){
          if (_this.countImgUrl==0) {
            _this.countDown()
            _this.timeShow = true;
            _this.predictionShow = false
            _this.predictionboxshow = false;
            _this.countShow = false
            _this.canvasShow = true
            if(_this.SurplusTime == 0){ // 
              _this.amount = 0
              _this.timeShow = false;
              _this.predictionShow = true;
              this.predictionboxshow = false;
              _this.openBag = true;
            }else {
              _this.addAudio(_this.mediaUrl + "/media/rain-voice.mp3")  //
              _this.init();
            }
            clearInterval(countTimer)
          };
          _this.countImgUrl--
          countImg.src = require('../../images/redbag-' + _this.countImgUrl +'.png')
        }, 1000)
      },
      // 
      robRedBag: function(){
          var _this = this;
          _this.addAudio()
          clearTimeout(_this.countDownTimer)  //
          this.$http({
            method: 'post',
            url: '/api/red_packages/draw',
            params: {id: _this.redbagId},
            headers: {accessToken: this.token}
          }).then(function(res){
            var data = res.body
            if(res.body.execErrCode==200){
              _this.addAudio(_this.mediaUrl + "/media/rain-getyes.mp3") //
              _this.amount = data.execData.amount
            }else {
              _this.addAudio(_this.mediaUrl + "/media/rain-getno.mp3")  //
              _this.amount = 0
            }
            _this.timeShow = false;
            _this.predictionShow = true;
            this.predictionboxshow = false;
            _this.openBag = true;
          })
      },
      //
      timeOut: function(){
        this.animateInit()
      },
      // 
      countDown: function(){
            var _this = this
            if(_this.SurplusTime==0 && _this.isHaveRedBag==true){
              _this.isHaveRedBag = false  //
              setTimeout(function(){
                _this.animateInit()
              }, 1000)
            }else if(_this.SurplusTime==0 && _this.readRainShow == true){
              _this.amount = 0
              _this.timeShow = false;
              _this.predictionShow = true;
              this.predictionboxshow = false;
              _this.openBag = true;
              _this.addAudio(_this.mediaUrl + "/media/rain-getno.mp3")  //
            }
            if(_this.SurplusTime>=0){
              _this.countDownTimer = setTimeout(function(){
                _this.countDown()
              },1000);
            }
            _this.SurplusTime--
      },
      /**/
      addAudio: function (src) {
        // if (this.redbagVoice == '') {
        //   this.redbagVoice = document.createElement("audio")
        //   this.redbagVoice.setAttribute('src', '');
        //   this.redbagVoice.src=src
        //   document.body.appendChild(this.redbagVoice);
        //   this.redbagVoice.play()
        // }else{
        //   this.redbagVoice.src=src
        //   this.redbagVoice.play()
        // };
        var audioNow = document.getElementsByTagName("audio")[0];
        if (audioNow == undefined) {
          var audio = document.createElement('audio');
          audio.setAttribute('src', '');
          document.body.appendChild(audio);
          audioNow = document.getElementsByTagName("audio")[0];
        }
        audioNow.pause();
        audioNow.src = src;
        audioNow.play();

      },
      remind: function(){
        var _this = this
        _this.redremindShow==true ? _this.redremindShow=false : _this.redremindShow=true
      },
      // 
      predictionCountDown: function(){
            var _this = this
            var leftTime = _this.distanceTime*1000;
            // d,h,m,s
            if( _this.distanceTime>=0){
              var d,h,m,s;
              if (leftTime>=0) {
                  d = Math.floor(leftTime/1000/60/60/24);
                  h = Math.floor(leftTime/1000/60/60%24);
                  m = Math.floor(leftTime/1000/60%60);
                  s = Math.floor(leftTime/1000%60);
              }
              _this.predictionM = m;
              _this.predictionS = s;
              if(_this.predictionM<10){
                _this.predictionM = '0' + _this.predictionM
              }
              if(_this.predictionS<10){
                _this.predictionS = '0' + _this.predictionS
              }
              var timer = setTimeout(function(){
                _this.predictionCountDown()
              },1000);
            }

            // 
            console.log(_this.distanceTime)
            if(_this.distanceTime == 0){
              _this.readRainShow = true;
              _this.predictionShow = true;
              _this.predictionboxshow = true //
              setTimeout(function(){
                _this.isHaveRedBag = true;
                setTimeout(function(){
                  _this.start()
                }, 1000)
                // _this.timeOut(1)
              }, 1000)
            }

            _this.distanceTime--
      },
      init: function(){
                var _this = this
                var moon = new Image();
                moon.src = require('../../images/redbag.png')
                moon.onload = function(){

                    var context;
                    var arr = new Array();
                    var starCount = 800;

                    var rains = new Array();
                    var rainCount =30;


                    //canvas
                    var stars = document.getElementById("stars");
                    var windowWidth = window.innerWidth; //
                    stars.width=windowWidth;
                    stars.height=window.innerHeight;
                    //context
                    var context = stars.getContext("2d");

                    /**/
                      var MeteorRain = function(){
                          this.x = -1;
                          this.y = -1;
                          this.length = -1;//
                          this.angle = 90; //
                          this.width = -1;//
                          this.height = -1;//
                          this.speed = 1;//
                          this.offset_x = 0;//
                          this.offset_y = 0;//
                          this.alpha = 1; //
                          this.color1 = "";//
                          this.color2 = "";  //
                        /************************************/
                        this.init = function (){ //
                          this.getPos();
                          this.alpha = 1;//
                          // this.getRandomColor();
                          //
                          var x = Math.random() * 80 + 150;
                          this.length = Math.ceil(x);
                          this.angle = Math.random()*50 + 75; //
                          x = Math.random()+.4;
                          this.speed = x; //
                          var cos = Math.cos(this.angle*3.14/180);
                          var sin = Math.sin(this.angle*3.14/180)*5;
                          this.width = this.length*cos ;  //
                          this.height = this.length*sin ;//
                          this.offset_x = this.speed*cos ;
                          this.offset_y = this.speed*sin;
                        }

                         /*********************************/
                        this.countPos = function () //
                        {
                            //,xy
                            this.x = this.x - this.offset_x;
                            this.y = this.y + this.offset_y;
                        }

                        /**********************************/
                        this.getPos = function () //
                        {
                            //200--1200

                            this.x = Math.random() * window.innerWidth; //
                            //600
                            this.y = Math.random() * window.innerHeight;  //
                        }
                         /*******************************/
                        this.draw = function () //
                        {

                            context.drawImage(moon, this.x + this.width,this.y - this.height,59,77);

                            // context.save();
                            // context.beginPath();
                        }
                        this.move = function(){
                            //
                            var x = this.x+this.width-this.offset_x;
                            var y = this.y-this.height;
                            //
                            this.countPos();
                            //
                            this.alpha -= 0.002;
                            //
                            this.draw();
                        }

                    }

                    //
                    function playRains(){
                        // context.clearRect(0,0, $("#stars").width(), ;
                        stars.height = stars.height
                        for (var n = 0; n < rainCount; n++){
                            var rain = rains[n];
                            rain.move();//
                            if(rain.y>window.innerHeight*3){//
                                rains[n] = new MeteorRain();
                                rains[n].init();
                            }
                        }
                        if(_this.canvasShow==true){
                            var redBagTimer = setTimeout(function(){
                                playRains()
                            },2);
                        }else{
                          $("#stars").fadeOut(0);
                          clearTimeout(redBagTimer)
                        }
                    }


                    //
                    for (var i=0;i<rainCount;i++) {
                        var rain = new MeteorRain();
                        rain.init();
                        rain.draw();
                        rains.push(rain);
                    }
                    playRains();//
                }
      }
    },
    mounted(){
      var _this = this
      //  document.addEventListener("pause", function () {
      //     _this.toast("");
      // }, false);
      // document.addEventListener("resume", function () {
      //     _this.toast("");
      // }, false);
      // id
      window.localStorage.setItem("redbagId",'')
      if (_this.$route.query.token) {
        _this.setCookie('intoToken', _this.$route.query.token, 3)
      }
      this.token = this.getCookie('intoToken');
      _this.isPlay()
      window.rainTimer = setInterval(function(){
        if(_this.readRainShow == false){
          _this.isPlay()
        }else {
          console.log("")
        }
      }, 20000)
    }
  }
</script>
<style scoped>
.readRain {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
  background: rgba(0,0,0,0.7);
}
.guide {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  right: 1rem;
  bottom: 1rem;
  margin: auto;
  width: 2.3rem;
  height: 2.87rem;
  transform: translateX(1.5rem);
  animation: guide 4s linear infinite;
}
.guide p {
  position: absolute;
  bottom: -.36rem;
  width: 100%;
  text-align: center;
  height: .36rem;
  line-height: .36rem;
  font-size: .36rem;
  color: #f8dd74;
}
.guide .quan {
  position: absolute;
  top: -.35em;
  left: -.35rem;
  width: 1.5rem;
  height: 1.5rem;
}
.guide .quan1 {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 1.5rem;
  height: 1.5rem;
  opacity: .3;
  background: #fff;
  border-radius: 50%;
  animation: quan 1s .5s linear infinite;
}
.guide .quan2 {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: .8rem;
  height: .8rem;
  background: #fff;
  opacity: .5;
  border-radius: 50%;
  animation: quan 1s .5s linear infinite;
}
.guide .hand {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 2.3rem;
  height: 2.87rem;
  background: url(../../images/redbag-hand2.png) no-repeat center/contain;
  animation: hands 1s linear infinite;
}
/**/
@keyframes guide {
  0% {
    opacity: 1;
  }
  25% {
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  51% {
    opacity: 0;
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
/**/
@keyframes hands {
  0% {
    background: url(../../images/redbag-hand2.png) no-repeat center/contain;
  }
  100% {
    background: url(../../images/redbag-hand1.png) no-repeat center/contain;
  }
}
/**/
@keyframes quan {
  0% {
    transform: scale(1);
    opacity: 0;
  }
  100% {
    transform: scale(1.3);
    opacity: 0.3;
  }
}
.prediction {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.prediction-box {
  padding-top: 3.3rem;
  margin: 0 auto;
  width: 9.7rem;
  height: 18.81rem;
  background: url(../../images/redbag-bg-prediction.png) no-repeat center/contain;
  transform: translate(0, -100%);
  transition: all 1s;
}
.prediction-box.down {
  transform: translate(0, 0);
}
.user-info {
  padding-left: .5rem;
  width: 100%;
  height: 3.68rem;
  /* background: pink; */
  /* opacity: 0.4; */
}
.user-info>div {
  height: 3.68rem;
  float: left;
  font-size: .7rem;
  color: #fdd382;
  line-height: 3.68rem;
  text-shadow: .03rem .05rem .05rem #FF0000;
}
.user-info .user-pic {
  position: relative;
  width: 2.94rem;
  height: 3.68rem;
  background: url(../../images/redbag-userpicbg.png) no-repeat center/contain;
}
.user-info .user-pic .bg {
  position: relative;
  z-index: 3;
  width: 2.94rem;
  height: 3.68rem;
  background: url(../../images/redbag-userpicbg.png) no-repeat center/contain;
}
.user-info .user-pic img {
  z-index: 2;
  position: absolute;
  top: .2rem;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 2.24rem;
  height: 2.24rem;
}
.pro-pic {
  position: relative;
  width: 4.05rem;
  height: 4.05rem;
  margin: 0 auto;
}
.pro-pic .bg{
  position: relative;
  width: 4.05rem;
  height: 4.05rem;
  z-index: 3;
  background: url(../../images/redbag-pro-picbg.png) no-repeat center/contain;
}
.pro-pic img {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  z-index: 2;
  width: 3.5rem;
  height: 3.5rem;
}
.pro-name {
  height: .8rem;
  padding: 0 .6rem;
  font-size: .5rem;
  color: #fdd382;
  text-align: center;
}
.prediction .txt {
  padding: .5rem .36rem;
  text-align: center;
  font-size: .8rem;
  color: #fdd382;
  line-height: 1rem;
  text-shadow: .03rem .05rem .05rem #FF0000;
}

.fhb {
  margin: 0 auto;
  width: 6.84rem;
  height: 2.27rem;
  background: url(../../images/redbag-fhb.png) no-repeat center/contain;
}

.go {
  position: fixed;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 7.28rem;
  height: 3rem;

}
.go img {
  margin: 0 auto;
  width: 3.21rem;
  height: 1.46rem;
}
.go .chuo {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 7.28rem;
  height: 1.28rem;
  background: url(../../images/redbag-chuo.png) no-repeat center/contain;
}
.open-redbag {

}
.open-redbag .havebag {
  width: 12.42rem;
  height: 13.9rem;
  margin-bottom: 2.4rem;
  background: url(../../images/redbag-havebag.png) no-repeat center/contain;
}
.open-redbag .havebag .text {
  color: #fc2522;
  font-size: .7rem;
  text-align: center;
  padding-top: 4.5rem;
}
.open-redbag .havebag .text .p1 {

}
.open-redbag .havebag .text .p2 {
  font-size: .5rem;
}
.open-redbag .havebag .text .p2 .money {
  font-size: .8rem;
  font-weight: 800;
  padding-right: .1rem;
}
.open-redbag button.sure {
  display: block;
  margin: 0 auto;
  width: 5.02rem;
  height: 1.13rem;
  background: url(../../images/redbag-btn-sure.png) no-repeat center/contain;
}

.open-redbag .nobag {
  position: relative;
  margin: 0 auto;
  width: 7.92rem;
  height: 13.9rem;
  margin-bottom: 2.4rem;
  background: url(../../images/redbag-nobag.png) no-repeat center/contain;
}
.open-redbag .nobag .text {
  font-size: .8rem;
  color: #ffcf01;
  text-align: center;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
}
.time {
  position: fixed;
  right: .2rem;
  top: .2rem;
  width: 1rem;
  height: 1rem;
  line-height: 1rem;
  text-align: center;
  border-radius: 50%;
  font-size: .6rem;
  font-weight: 600;
  color: #ea441c;
  background: #D8C5AB;
  text-indent: -.15rem;
}

.red-remind {
  position: fixed;
  /*top: 3.55rem;*/
  top: 9rem;
  right: -2.8rem;
  width: 4.98rem;
  height: 1.21rem;
  text-indent: .95rem;
  line-height: 1.51rem;
  background: url(../../images/redbag-remind-bg.png) no-repeat center/contain;
  z-index: 99999;
  font-size: .42rem;
  color: #fff7b6;
  text-shadow: 5px 5px 5px #793d25;
  transform: translate(0, 0);
  transition: all 1s;
}
.red-remind.active {
  transform: translate(-2.7rem, 0);
}
</style>



