<template>
  <!---->
  <div class="bubble-notice">
    <div class="username text-overflow"><span> {{notice.user.nick_name}}</span></div>
    <div class="text text-overflow">{{notice.chest_prizes[0].name}}</div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        notice: {user: {nick_name: ''}, chest_prizes: [{name: ''}]},
        fixedData: [
          {user: {nick_name: ''}, chest_prizes: [{name: ''}]},
          {user: {nick_name: ''}, chest_prizes: [{name: '50'}]},
          {user: {nick_name: ''}, chest_prizes: [{name: '100'}]},
          {user: {nick_name: ''}, chest_prizes: [{name: 'BeatsX'}]},
          {user: {nick_name: ''}, chest_prizes: [{name: 'MCM'}]},
          {user: {nick_name: ''}, chest_prizes: [{name: '20'}]},
          {user: {nick_name: ''}, chest_prizes: [{name: ' 7 32G'}]},
          {user: {nick_name: ''}, chest_prizes: [{name: ' 60G '}]},
          {user: {nick_name: ''}, chest_prizes: [{name: ' U'}]}
        ]
      }
    },
    methods: {
      request: function () {
        this.$http({
          method: 'get',
          url: '/api/chest_boxs/notice?q[prize_type_in][]=1&q[prize_type_in][]=7'
        }).then(function (res) {
          let data = res.body;
          if (data.execErrCode == 200) {
            let allNotice = data.execData.chest_records, notice = [];
            for (let i = 0, len = allNotice.length; i < len; i++) {
              if (allNotice[i].chest_prizes.length > 0) {
                notice.push(allNotice[i]);
              }
            }
            if (notice.length > 0) {
              let n = Math.floor(Math.random() * notice.length);
              this.notice = notice[n];
              this.keyframe();
            }
          }
        }, function () {
          let n = Math.floor(Math.random() * this.fixedData.length);
          this.notice = this.fixedData[n];
          this.keyframe();
        })
      },
      keyframe: function () {
        let bubbleNotice = document.getElementsByClassName('bubble-notice')[0];
        bubbleNotice.className = 'bubble-notice slide';
        setTimeout(function () {
          bubbleNotice.className = 'bubble-notice';
        }, 2300)
      }
    },
    mounted(){
      let _this = this;
      window.timer10 = setInterval(function () {
        _this.request()
      }, 7600)
    },
    beforeDestroy: function () {
      clearInterval(window.timer10)
    }
  }
</script>
<style scoped>
  /*--*/
  .bubble-notice {
    position: fixed;
    left: 0;
    bottom: 34%;
    z-index: 55;
    width: 5.25rem;
    height: 1.65rem;
    font-size: .39rem;
    padding: .24rem .27rem;
    transform: translate3d(-100%, 0, 0);
    transition: transform .3s;
    background: rgba(0, 0, 0, .6);
    border-radius: 0 .83rem .83rem 0;
  }

  .bubble-notice.slide {
    transform: translate3d(0, 0, 0);
  }

  .bubble-notice .username {
    color: #fff;
    margin-bottom: .12rem;
  }

  .bubble-notice .text {
    color: #fae020;
  }
</style>



